.footer {
    display: flex;
    justify-content: space-around;
    background-color: rgb(60, 58, 58);
    color: white;
    text-align: center;
    font-size: 25px;
    bottom: 0;
}
.footer > a {
    color: rgb(63, 193, 63);
}
.footer > a > img {
    border-radius: 100%;
    width: 40px;
    height: 40px;
}